@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600);
* {
  box-sizing: border-box; }

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400; }

.container {
  margin: 0 auto;
  width: 50rem; }

h1, h2, h3, h4, h5 {
  font-weight: 600; }

h1 {
  font-size: 1.8rem; }

h2 {
  font-size: 1.6rem; }

h3 {
  font-size: 1.4rem; }

h4 {
  font-size: 1.2rem; }

h5 {
  font-size: 1rem; }

button {
  background: #9b59b6;
  border: none;
  color: white;
  display: block;
  font-size: 1.2rem;
  padding: 0.8rem 1rem;
  width: 100%; }

nav {
  background: #1abc9c;
  padding: 1rem; }

nav div.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

nav a {
  color: white;
  text-decoration: none; }

nav ul li {
  display: inline; }

nav ul li a {
  color: white;
  padding: 0 1rem;
  text-decoration: none; }

.user-form {
  border: 1px solid #ecf0f1;
  border-radius: 10px;
  display: block;
  margin: 10rem auto;
  padding: 3rem 2rem;
  width: 28rem; }

.user-form h3 {
  margin-bottom: 2rem;
  text-align: center; }

.user-form input {
  border: 1px solid #ecf0f1;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.08);
  display: block;
  font-size: 1.2rem;
  margin: 0.75rem 0;
  padding: 0.75rem 1.5rem;
  width: 100%; }

